import { ComponentDecoratorGenerator } from './component.js';
import { ComponentObjectModifier } from './object.js';
/**
 * Component装饰器
 *
 */
export function Component(classTargetOrOption) {
  "use strict";
  /*
   * @Component
   * class A {}
   *
   * 需要return一个 ClassDecorator
   */
  if ( typeof classTargetOrOption === 'function' ) {
    return ComponentDecoratorGenerator()(classTargetOrOption)
  }
  /*
   * @Component({})
   * class A {}
   *
   * 需要return 修改完的class
   */
  else {
    return ComponentDecoratorGenerator(classTargetOrOption);
  }

}


export function ObjectFunc(objectFuncOrOption) {
  "use strict";

  if ( typeof objectFuncOrOption === 'function' ) {
    return ComponentObjectModifier()(objectFuncOrOption)
  }
  /*
   * @Component.Object({})(function (){})
   *
   *
   */
  else {
    return ComponentObjectModifier(objectFuncOrOption);
  }
}